@import "../colors/Colors";

.date-picker-container {
    display: flex;
    background: $cost-blue;
    color: white;
    padding: 4px 8px;
    height: 56px;
    margin-bottom: 10px;
    position: relative;
    .react-datepicker__tab-loop {
        position: absolute;
        top: 0;
    }
    &.date-picker-in-modal {
        height: 50px;
        padding: 0;
        margin-bottom: 0;
        input {
            padding: 0;
        }
    }
    .paginate {
        width: 40px;
        &.visible {
            cursor: pointer;
            background-size: contain;
        }
        &.left.visible {
            background-image: url("../assets/ic_chevron_left_white_48px.svg");
            background-repeat: no-repeat;
            background-position: center center;
        }
        &.left {
            margin-right: 20px;
        }
        &.right.visible {
            background-image: url("../assets/ic_chevron_right_white_48px.svg");
            background-repeat: no-repeat;
            background-position: center center;
        }
        &.right {
            margin-left: 20px;
        }
    }
    > div,
    .react-datepicker__input-container,
    .react-datepicker-wrapper {
        width: 100%;
        div {
            font-size: 14px;
            font-family: JmhTypewriter, sans-serif;
        }
    }
    .react-datepicker__current-month {
        font-size: 16px;
    }
    .react-datepicker-popper {
        .react-datepicker__month {
            height: 216px;
        }
    }
    .react-datepicker__navigation {
        top: 10px;
    }
    .react-datepicker,
    .react-datepicker__day:hover,
    .react-datepicker__time-container--with-today-button,
    .react-datepicker__day--keyboard-selected {
        border-radius: 0;
    }
    .react-datepicker-popper {
        z-index: 15;
    }
    .react-datepicker-popper[data-placement^="top"] {
        margin-bottom: 12px;
    }
    .react-datepicker-popper[data-placement^="bottom"] {
        margin-top: 13px;
    }
    &.date-picker-in-modal {
        .react-datepicker-popper[data-placement^="top"] {
            margin-bottom: 9px;
        }
    }
    &.date-picker-in-modal {
        .react-datepicker-popper[data-placement^="bottom"] {
            margin-top: 9px;
        }
    }
    .react-datepicker__day-name,
    .react-datepicker__day,
    .react-datepicker__time-name {
        margin: 2px;
        width: 32px;
        height: 32px;
        padding: 3px 7px 7px;
    }
    .react-datepicker__day--keyboard-selected {
        background: $cost-blue;
    }
    .react-datepicker__day--selected {
        border-radius: 0;
        background: $dark-gray;
        color: black;
    }
    .react-datepicker__day--today {
        background: $cost-blue;
        color: white;
    }
    .react-datepicker__header {
        background: $light-gray;
    }
    .react-datepicker__navigation {
        border: 0.5rem solid transparent;
        border-image-outset: 0;
        border-image-repeat: stretch;
        border-image-slice: 100%;
        border-image-source: none;
        border-image-width: 1;
        &.react-datepicker__navigation--previous {
            border-right-color: #ccc;
        }
        &.react-datepicker__navigation--next {
            border-left-color: #ccc;
        }
    }
    .react-datepicker-popper[data-placement^="top"]
        .react-datepicker__triangle {
        border-bottom: none;
        border-top-color: #f0f0f0;
    }
    input {
        border: 0;
        background: $cost-blue;
        color: white !important;
        width: 100%;
        height: 48px;
        text-align: center;
        cursor: pointer;
        font-size: 20px;
        padding-top: 3px;
        font-family: JmhTypewriter;
    }
}
